    import choose from "../../components/template/image/choose.vue"
    import compress from "../../components/template/image/compress.vue"
	
    export default {
        name:'newsPublish',
        components:{
        	choose,
            compress
        },
        data() {
            return {
                isYasuo:true,
                count:6,
                maxwh:280,
                quality:1, 
                
                msgContents: ["界面显示错乱", "启动缓慢，卡出翔了", "UI无法直视，丑哭了", "偶发性崩溃"],
                stars: [1, 2, 3, 4, 5],
                imgList: ["../../static/img/home.png"],
                sendDate: {
                    score: 0,
                    content: "",
                    contact: ""
                }
            }
        },
        onLoad() {

        },
        methods: {
            compressImg(e){
              console.log(e)  
            },
            changeIndicatorDots(e){
            this.isYasuo = !this.isYasuo
            },
            fileChange(e){
              this.imgList=e;
                  //             YCImg.canvasToBase64(e)
                  // .then(e => {
                  //     // console.log(JSON.stringify(e))
                  //     this.value = e;
                  //     this.confirm();
                  //     // console.log(e)
                  // })
                  // .catch(e => {
                  //     uni.showToast({
                  //         title: '失败！' + e.message,
                  //         icon: 'none',
                  //         duration: 1000
                  //     });
                  // })
            },
            chooseStar(e) { //点击评星
                this.sendDate.score = e;
            },
            previewImage() { //预览图片
                uni.previewImage({
                    urls: this.imgList
                });
            },
            send() { //发送提交
                 console.log(JSON.stringify(this.sendDate));
                
                function requst(imgs,data){
					
                    console.log(JSON.stringify(imgs));
					data.qid=1;
					
					uni.request({
						url: "http://192.168.43.202:8081/user-note/addContent",
						data:data,
						method: "GET",
						dataType: 'json',
						success(res) {
							console.log(res);
							if(res.data.code==200)
							{
								var data=res.data.data;
								//data.id  //就是添加的用户的笔记的Id 
								
								uni.uploadFile({
								    url: "http://192.168.43.202:8081/user-note/addImg?nid="+data.id,
								    files: imgs,
						
								    success: (res) => {
								        if (res.data.code==200) {
								            uni.showToast({
								                title: "反馈成功!"
								            });
								            this.imgList = [];
								            this.sendDate = {
								                score: 0,
								                content: "",
								                contact: ""
								            }
								        }
								    },
								    fail: (res) => {
								        // console.log(res)
								    }
								});
								
								
					
							}
						},
						fail(res) {
							console.log(res.data)
						},
						complete() {
					
						}
					})
                                    
                }
                
                if(this.isYasuo){

                    this.$refs.compress.yasuoImg(this.imgList).then(e=>{
                        // console.log([this.imgList,e])
                        let imgs = e.map((value, index) => {
                            //var base64= value.tempFilePath
                            return {
                                name: "image" + index,
                                uri:value.path,
                                base64:value.tempFilePath
                                
                            }
                        })
                        // console.log(imgs)
                        requst(imgs,this.sendDate)
                    })
                }else{
                    
                    let imgs = this.imgList.map((value, index) => {
                        return {
                            name: "image" + index,
                            uri:value.path,
                            base64:value.tempFilePath
                        }
                    })
                    requst(imgs,this.sendDate)
                }
            }
        }
    }
